.card {
    margin: 10px;
    position: relative;
    width: 428px;
    -webkit-box-shadow: 0 5px 9px rgb(125, 125, 125), 0 5px 9px rgb(125, 125, 125);
            box-shadow: 0 5px 9px rgb(125, 125, 125), 0 5px 9px rgb(125, 125, 125);
    display: -ms-flexbox;
    display: flex;
    background-color: rgb(0, 0, 0, 0)
  }

  .img-container{
    top: 10px;
    padding: 0;
    overflow: hidden;
    border-radius: 5px;
  }


  img {
    max-width: 100%;
    position: relative;
  }

  .projectName{
    /* -webkit-transform: rotate(-45deg); */
    /* transform: rotate(-45deg); */
    /* -ms-transform: rotate(-45deg); */
    background-image: -webkit-gradient(linear,left center,right center,from(black),to(black));
    /* transition: background-color rgb(0,0,0,0.8); */
    position: relative;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: center;
        justify-content: center;
    width: 100%;
    top:0;
    padding: 0;
    /* border-bottom-radius: 5px; */
  }

  a , a:hover, a:active{
    color: white;
    text-decoration: none;
  }


