.wrapper {
  background: #19324b;  
  background: -webkit-linear-gradient(to top, #fff, #19324b);
  background: -webkit-gradient(linear, left top, left bottom, from(#fff), to(#19324b));
  background: -webkit-linear-gradient(top, #fff, #19324b);
  background: -o-linear-gradient(top, #fff, #19324b);
  background: linear-gradient(to bottom, #fff, #19324b); 
  height: 100%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  -ms-flex-line-pack: center;
      align-content: center; 
  overflow: auto;
  bottom: 0;
}



